<template>
            <el-dialog
                  :title="title"
                  center="center"
                  :visible.sync="dialogVisible"
                  width="40%"
                  :before-close="handleClose">
                  <el-form :rules="rules" ref="ruleForm" :model="query" label-width="98px">
                      <div class="tian">
                          <el-form-item label="账号：" prop="username">
                            <el-input v-model="query.username"></el-input>
                          </el-form-item>
                          <el-form-item label="密码：" prop="pass">
                            <el-input v-model="query.pass" type="password"></el-input>
                          </el-form-item>
                          <el-form-item label="确认密码：" prop="passpwd">
                            <el-input v-model="query.passpwd" type="password" ></el-input>
                          </el-form-item>
                          <el-form-item label="姓名：" prop="name">
                            <el-input v-model="query.name"></el-input>
                          </el-form-item>
                          <el-form-item label="联系方式：" prop="tel">
                            <el-input v-model="query.tel"></el-input>
                          </el-form-item>
                          <el-form-item label="角色" prop="typeid">
                            <el-select v-model="query.typeid" placeholder="请选择">
                              <el-option v-for="(imet,index) in list" :key="index" :label="imet.name" :value="imet.id"></el-option>
                            </el-select>
                          </el-form-item>

                      </div>
                  </el-form>
                      <span slot="footer" class="dialog-footer">
                        <el-button @click="satlop">取 消</el-button>
                        <el-button type="primary" @click="submit('ruleForm')">确 定</el-button>
                      </span>
                  
              </el-dialog>
</template>

<script>
export default {
    name:"addAdministrator",
    props:{
        dialogVisible:{
            type:Boolean,
            default:false
        },
        modcom:{
          type:Object,
          default:()=>{}
        }
    },
    data(){
            // 账号
            var username = (rule, value, callback) => {
            let reg = /^[^\d][\u2E80-\u9FFFA-z-_.\d]{1,17}$/
            if(!reg.test(value)){
            return callback(new Error('账号是由汉字、字母2-18位组成的'))
            }
            callback()
        };
        //   密码
        var pass = (rule, value, callback) => {
          // console.log(this.query.pass)
            // if(value){
            //   callback()
            // }else{
              let reg = /^[\w]{1,18}$/
              if(!reg.test(value)){
                return callback(new Error('密码是由数字、字母6-18位组成的'))
              }
              callback()
            // }
      };
      //   确认密码
        var passpwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.query.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      //姓名
      // var name = (rule, value, callback) => {
      //   let reg = /^[\u4e00-\u9fa5]{2,4}$/
      //   if(!reg.test(value)){
      //     return callback(new Error('姓名由汉字2-4个字组成的'))
      //   }
      //   callback()
      // };
    //   联系方式
        var tel = (rule, value, callback) => {
        let reg = /^1[356789]\d{1,9}$/
        if(!reg.test(value)){
          return callback(new Error('手机号由数字12个字组成的'))
        }
        callback()
      };
        return{
            title:"添加管理员",
            list:[],
            query:{
              // passpwd:''
            },
            rules: {
            username: [
                { required: true, message: '请输入账号', trigger: 'blur' },
                { validator: username, trigger: 'blur' }
            ],
            // pass: [
            //   { required: true, message: '请输入密码', trigger: 'blur' },
            //   { validator: pass, trigger: 'blur' }
            // ],
            // passpwd:[
            //     { required: true, message: '请输入确认密码', trigger: 'blur' },
            //     { validator: passpwd, trigger: 'blur' }
            // ],
            name: [
              { required: true, message: '请输入真实姓名', trigger: 'blur' },
              // { validator: name, trigger: 'blur' }
            ],
             tel: [
              { required: true, message: '请输入手机号', trigger: 'blur' },
              { validator: tel, trigger: 'blur' }
            ],
            typeid: [
              { required: true, message: '请选择角色', trigger: 'blur' }
            ],
          }
        }
    },
    created(){
        this.getadmin()
    },
    updated(){
      this.title = this.modcom.id?'编辑管理员':'添加管理员'
      this.query = this.modcom?this.modcom:{}
      // console.log(222,this.modcom,this.query);
    //  this.query.passpwd = this.query.pass

    },
    methods:{
    satlop(){
        this.$emit('chansgedialogVisible',false)
    },
    // 添加
     submit(formName){
              //  console.log(this.query.pass)
              
    this.$refs[formName].validate(async (valid) => {
          if (valid) {
                  delete this.query.passpwd
                  // this.query.pass = {}
                  // this.$emit('reght')
                  let res = await this.http.post('/administrator/add',this.query)
                  if(res.errCode == 10000){
                    this.$message.success('添加成功')
                    this.$emit('chansgedialogVisible', false);

                  }else{
                    this.$message.error(res.errMsg)
                  }
                  this.$emit('reseteditform')
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
    },

    // 角色列表
      async getadmin(){
        let res = await this.http.get('/administratorrole/list',{page:1,psize:270})
        if(res.errCode == '10000'){
          this.list = res.data.list
          // console.log(res)
        }
      },
      // 添加
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(() => {
            // done();
            this.$emit('reseteditform')
           this.$emit('chansgedialogVisible', false);
          })
          .catch(() => {});
      },
    }
}
</script>

<style lang="less" scoped>
@import '../assets/css/admin.css';
</style>