<template>
  <div>
    <el-card class="box-card">
      <span>管理员管理</span>
    </el-card>
    <el-row>
      <el-col :span="24">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="关键字" style="width: 30%">
            <el-input @keyup.native.enter="seratch" clearable @clear="seratch" v-model="form.key" placeholder="关键字"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="seratch">查询</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row class="butt">
      <el-button type="primary" v-if="$power.haveown(power,'添加管理员')" @click="dialogVisible = true" >添加</el-button>
    </el-row>

    <!-- 表格 -->
      <Table :loading="loading" :tableData="tableData" :tableCouml="tableCouml">
          <template #option="data">
            <el-button size="mini" type="success" v-if="$power.haveown(power,'修改管理员')"  @click="mod(data.data)">修改</el-button>
            <el-button size="mini" type="danger" v-if="$power.haveown(power,'删除管理员')" @click="handleDelete(data.data.id)">删除</el-button>
          </template>
      </Table>

    <!-- 分页 -->
        <Paging :form="form" :total="total" @paging="paging"></Paging>

        <!-- 添加 -->
              <add-administrator
              @reseteditform="reseteditform"
              :modcom="modcom" 
              :dialogVisible="dialogVisible" 
              @chansgedialogVisible="chansgedialogVisible"></add-administrator>
  </div>
</template>
<script>
import addAdministrator from '@/components/addAdministrator.vue'
import Table from '@/components/commts/Table.vue'
import Paging from '@/components/commts/Paging.vue'
export default {
  data() {
    return {
      loading: true,
      form: {
        page:1,
        psize:5
      },
      tableData: [],
      tableCouml:[
        {prop:"id",label:'ID',width:"180"},
        {prop:"username",label:'登录账号',width:"180"},
        {prop:"name",label:'姓名',width:"230"},
        {prop:"tel",label:'手机号',width:"230"},
        {prop:"typeName",label:'类型',width:"230"},
        {label:'操作',isshow:true,showname:'option'},
      ],
      total:0,
      dialogVisible: false,
      modcom:{},
      power:[]    
    };
  },
  components:{
    addAdministrator,
    Table,
    Paging
  },
  created(){
    this.power = this.$power.getownpriv('管理员管理')
    // console.log(this.$power.getownpriv('管理员管理'))

    this.getimet()
    
  },
  methods:{
    // 刷新页面
    paging(){
      this.getimet()
    },
    // 清空
    reseteditform(){
      this.modcom = {}
    },
    // reght(){
    //   console.log(this.modcom)
    // },
    // 修改
    mod(val){
      // console.log(val)
      this.modcom = JSON.parse(JSON.stringify(val))
      this.dialogVisible = true
      this.modcom.pass=""
    },
    // 删除
     handleDelete(val){
       this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
            let res = await this.http.get('/administrator/delete',{id:val})
            if(res.errCode == '10000'){
                this.$message.success('删除成功')
                this.getimet()
            }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    // 传递
    chansgedialogVisible(val){
      this.dialogVisible = val
      this.getimet()
    },
    //查询
    seratch(){
      this.getimet()
    },
    
      // 列表
      async getimet(){
        let res = await this.http.get('/administrator/list',this.form)
        // console.log(res)
        if(res.errCode == '10000'){
          this.tableData = res.data.list
          this.total = res.data.counts
          this.loading = false
        }
      },
      // 添加
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(() => {
            done();
          })
          .catch(() => {});
      },
  },
};
</script>

<style lang="less" scoped>
@import "../assets/css/admin.css";
</style>