<template>
             <div class="block">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="this.query.page"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="this.query.psize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
        </div>
</template>

<script>
export default {
    name:"Paging",
    props:{
        query:{
            type:Object,
            require:()=>{}
        },
        total:{
            type:Number,
            require:()=>{}
        },
    },
    data(){
        return{

        }
    },
    methods:{
         handleSizeChange(val) {
            this.query.psize = val
            this.loading = false
            this.$emit('paging',);
        },
        handleCurrentChange(val) {
            this.query.page = val
            this.$emit('paging',);
            this.loading = false
        }
    }
}
</script>

<style lang="less" scoped>
@import '@/assets/css/admin.css';
</style>