<template>
            <el-table :data="tableData" style="width: 100%" v-loading="loading" element-loading-background="rgba(0, 0, 0, 0)" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"> </el-table-column>
                    <!-- <el-table-column :key="index"><el-image :style="imet.style" :src="imet.src" fit="fill"></el-image> </el-table-column> -->
                <template v-for="(imet,index) in tableCouml" >
                    <el-table-column v-if="!imet.isshow" :key="index" :prop="imet.prop" :label="imet.label" :width="imet.width">  </el-table-column>
                    <el-table-column :label="imet.label" :key="index" v-else>
                        <!-- 作用域插槽 -->
                        <template slot-scope="scope">
                            <!-- 插槽处 slot -->
                            <slot :name="imet.showname" :data="scope.row"></slot>
                        </template>
                    </el-table-column>
                    </template>
            </el-table>
</template>
<script>

export default {
    name:"tablecom",
    // 接值
    props:{
        loading:{
            type:Boolean,
            default:()=>true
        },
        tableData:{
            type:Array,
            default:()=>[]
        },
        tableCouml:{
            type:Array,
            default:()=>[]
        }
    },
    data(){
        return{
            // tabledata:[]
        }
    },
    created(){
        // console.log(this.tabledata = this.tableData)
    },
    methods:{
        handleSelectionChange(val){
            // console.log(val)
            val.forEach(imet => {
                // this.ids.push(imet.id)
                this.$emit('idshow',imet.id)
            });
            
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../assets/css/admin.css';
</style>